import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import socket from '../utils/socket';
import './Result.css'; // External CSS for styling the podium and leaderboard

const Result = () => {
    const [results, setResults] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [showPodium, setShowPodium] = useState(false);
    const [showLeaderboard, setShowLeaderboard] = useState(false);
    const gameCode = sessionStorage.getItem('gameCode');
    const navigate = useNavigate();

    useEffect(() => {
        console.log('Listening for resultsReady event...');

        const handleResultsReady = ({ results }) => {
            console.log('Received results:', results);
            setResults(results);
            setLoading(false);

            // Display podium with a delay for suspense
            setTimeout(() => setShowPodium(true), 1000); // Show podium after 1 second
            setTimeout(() => setShowLeaderboard(true), 5000); // Show leaderboard after 5 seconds
        };

        socket.on('resultsReady', handleResultsReady);

        // Retry fetching results periodically if loading takes too long
        const retryFetchWinners = setInterval(() => {
            if (loading) {
                console.log('Retrying fetchWinners...');
                socket.emit('fetchWinners', { gameCode }, ({ success, winners, error }) => {
                    if (success) {
                        console.log('Fetched winners from backend:', winners);
                        setResults(winners);
                        setLoading(false);
                        clearInterval(retryFetchWinners); // Stop retrying once successful
                        setTimeout(() => setShowPodium(true), 1000);
                        setTimeout(() => setShowLeaderboard(true), 5000);
                    } else {
                        console.error('Error fetching winners:', error);
                        setError('Unable to fetch results.');
                    }
                });
            }
        }, 3000); // Retry every 3 seconds

        // Cleanup on unmount
        return () => {
            console.log('Cleaning up listeners...');
            socket.off('resultsReady', handleResultsReady);
            clearInterval(retryFetchWinners);
        };
    }, [gameCode]);

    if (loading) {
        return <div className="loading">Loading results...</div>;
    }

    if (error) {
        return <div className="error">Error: {error}</div>;
    }

    if (!results.length) {
        console.log('No results found; forcing reload...');
        window.location.reload(); // Force reload if no results
        return null;
    }

    const podium = results.slice(0, 3); // Top 3 players for the podium
    const restOfPlayers = results.slice(3); // Remaining players for the leaderboard

    return (
        <div className="result-page">
            <h1 className="game-over-title"> Game Over </h1>

            {/* Podium Section */}
            {showPodium && (
                <div className="podium">
                    {podium.map((player, index) => (
                        <div key={player.id} className={`podium-place place-${index + 1}`}>
                            <h2>{index + 1 === 1 ? '' : index + 1 === 2 ? '' : ''}</h2>
                            <h3>{player.username}</h3>
                            <p>Final Score: {player.finalScore}</p>
                        </div>
                    ))}
                </div>
            )}

            {/* Leaderboard Section */}
            {showLeaderboard && (
                <div className="leaderboard">
                    <h2>Leaderboard</h2>
                    <ul>
                        {results.map((player, index) => (
                            <li key={player.id} className={`leaderboard-item ${index < 3 ? 'top-3' : ''}`}>
                                {index + 1}. {player.username} - Final Score: {player.finalScore}
                            </li>
                        ))}
                    </ul>
                </div>
            )}

            <button className="home-button" onClick={() => navigate('/')}>
                Return to Home
            </button>
        </div>
    );
};

export default Result;
