// src/components/CreateGame.js
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import socket from '../utils/socket';
import Cookies from 'js-cookie';
import './CreateGame.css';

function CreateGame() {
    const [error, setError] = useState('');
    const navigate = useNavigate();

    const generateGameCode = () => {
        return Math.random().toString(36).substring(2, 6).toUpperCase();
    };

    const createGame = (e) => {
        e.preventDefault();
        const newGameCode = generateGameCode();

        socket.emit('createGame', { gameCode: newGameCode }, (response) => {
            if (response.success) {
                // After creating the game, join as host
                socket.emit('joinHost', 
                    { 
                        gameCode: newGameCode,
                        playerToken: null // First time joining as host, no token yet
                    }, 
                    (joinResponse) => {
                        if (joinResponse.success) {
                            // Store the host token in cookies
                            if (joinResponse.playerToken) {
                                console.log('Storing host token for game:', newGameCode);
                                Cookies.set(`hostToken_${newGameCode}`, joinResponse.playerToken, {
                                    expires: 1, // Expires in 1 day
                                    secure: process.env.NODE_ENV === 'production',
                                    sameSite: 'strict'
                                });
                            }

                            // Store game information in session storage
                            sessionStorage.setItem('gameCode', newGameCode);
                            sessionStorage.setItem('isHost', 'true');

                            // Store initial game state if provided
                            if (joinResponse.gameState) {
                                sessionStorage.setItem('gameState', JSON.stringify(joinResponse.gameState));
                            }

                            navigate('/lobby');
                        } else {
                            // Handle join failure
                            console.error('Failed to join as host:', joinResponse.error);
                            setError(joinResponse.error || 'Failed to join game');
                            
                            // Clean up any stored data
                            Cookies.remove(`hostToken_${newGameCode}`);
                            sessionStorage.removeItem('gameCode');
                            sessionStorage.removeItem('isHost');
                        }
                    }
                );
            } else {
                console.error('Failed to create game:', response.error);
                setError(response.error || 'Failed to create game');
            }
        });
    };

    const handleError = (error) => {
        setError(error);
        setTimeout(() => setError(''), 5000); // Clear error after 5 seconds
    };

    return (
        <div className="create-game-container">
            <div className="create-game-header">
                <h1 className="interactive-title">
                    <span className="title-part">Create</span> Your <span className="title-part">Game</span>
                </h1>
                <p className="subtitle">Host your own adventure. It starts here.</p>
            </div>
            {error && <div className="error-message">{error}</div>}
            <div className="form-container">
                <button 
                    onClick={createGame} 
                    className="create-button"
                    disabled={!!error}
                >
                    Start the Game
                </button>
            </div>
        </div>
    );
}

export default CreateGame;
