import React, { useEffect, useState, useRef } from 'react';
import socket from '../utils/socket';

const TopBar = ({ gameCode, isHost, players }) => {
    const username = sessionStorage.getItem('username');
    const currentPlayer = players.find(p => p.username === username);

    const [previousStats, setPreviousStats] = useState({}); // To track previous stats

    const applyStatChangeGlow = (element, isIncrease) => {
        if (!element) return;
        element.classList.add(isIncrease ? 'stat-increase' : 'stat-decrease');
        setTimeout(() => {
            element.classList.remove('stat-increase', 'stat-decrease');
        }, 1000); // Animation duration matches CSS transition
    };

    useEffect(() => {
        players.forEach(player => {
            const prevStats = previousStats[player.id] || {};

            // Compare and highlight stat changes
            const elements = {
                money: document.querySelector(`.player-money[data-id="${player.id}"]`),
                hp: document.querySelector(`.player-hp[data-id="${player.id}"]`),
                int: document.querySelector(`.player-int[data-id="${player.id}"]`),
                str: document.querySelector(`.player-str[data-id="${player.id}"]`),
                char: document.querySelector(`.player-char[data-id="${player.id}"]`),
            };

            if (prevStats.money !== undefined && prevStats.money !== player.money) {
                applyStatChangeGlow(elements.money, player.money > prevStats.money);
            }
            if (prevStats.hp !== undefined && prevStats.hp !== player.character.hp) {
                applyStatChangeGlow(elements.hp, player.character.hp > prevStats.hp);
            }
            if (prevStats.int !== undefined && prevStats.int !== player.character.int) {
                applyStatChangeGlow(elements.int, player.character.int > prevStats.int);
            }
            if (prevStats.str !== undefined && prevStats.str !== player.character.str) {
                applyStatChangeGlow(elements.str, player.character.str > prevStats.str);
            }
            if (prevStats.char !== undefined && prevStats.char !== player.character.char) {
                applyStatChangeGlow(elements.char, player.character.char > prevStats.char);
            }
        });

        // Update previous stats for the next comparison
        const updatedStats = players.reduce((acc, player) => {
            acc[player.id] = {
                money: player.money,
                hp: player.character.hp,
                int: player.character.int,
                str: player.character.str,
                char: player.character.char,
            };
            return acc;
        }, {});
        setPreviousStats(updatedStats);
    }, [players]);

    return (
        <div className="top-bar">
            <div className="game-info">
                <h2>Game Code: {gameCode}</h2>
                {isHost && <div className="host-indicator">You are the host</div>}
            </div>
            {isHost ? (
                <div className="players-list">
                    {players.map((player) => (
                        <div key={player.id} className="player-card">
                            <div className="player-header">
                                <span className="player-name">{player.username}</span>
                            </div>
                            <div className="player-details">
                                <div className="player-stats-row">
                                    <div className="player-money" data-id={player.id}>
                                         ${player.money || 0}
                                    </div>
                                    <div className="player-hp" data-id={player.id}>
                                         HP: {player.character?.hp || 0}
                                    </div>
                                </div>
                                {player.gender && (
                                    <div className="player-gender">
                                        {player.gender === 'boy' ? '' : ''} {player.gender}
                                    </div>
                                )}
                                <div className="player-stats">
                                    <div className="stats-row">
                                        <span className="player-int" data-id={player.id}>
                                             INT: {player.character?.int || 0}
                                        </span>
                                        <span className="player-str" data-id={player.id}>
                                             STR: {player.character?.str || 0}
                                        </span>
                                        <span className="player-char" data-id={player.id}>
                                             CHA: {player.character?.char || 0}
                                        </span>
                                    </div>
                                </div>
                                {player.job && player.job.name && (
                                    <div className="player-job">
                                         Job: {player.job.name}
                                        <div className="job-description">
                                             {player.job.description}
                                        </div>
                                        <div className="job-effects">
                                            <span> Salary: ${player.job.effects?.money || 0}</span>
                                        </div>
                                    </div>
                                )}
                            </div>
                        </div>
                    ))}
                </div>
            ) : (
                <div className="player-info">
                    <div className="player-card">
                        <div className="player-header">
                            <span className="player-name">{username}</span>
                        </div>
                        <div className="player-details">
                            <div className="player-stats-row">
                                <div className="player-money" data-id={currentPlayer?.id}>
                                     ${currentPlayer?.money || 0}
                                </div>
                                <div className="player-hp" data-id={currentPlayer?.id}>
                                     HP: {currentPlayer?.character?.hp || 0}
                                </div>
                            </div>
                            {currentPlayer?.gender && (
                                <div className="player-gender">
                                    {currentPlayer.gender === 'boy' ? '' : ''} {currentPlayer.gender}
                                </div>
                            )}
                            <div className="player-stats">
                                <div className="stats-row">
                                    <span className="player-int" data-id={currentPlayer?.id}>
                                         INT: {currentPlayer?.character?.int || 0}
                                    </span>
                                    <span className="player-str" data-id={currentPlayer?.id}>
                                         STR: {currentPlayer?.character?.str || 0}
                                    </span>
                                    <span className="player-char" data-id={currentPlayer?.id}>
                                         CHA: {currentPlayer?.character?.char || 0}
                                    </span>
                                </div>
                            </div>
                            {currentPlayer?.job && currentPlayer.job.name && (
                                <div className="player-job">
                                     Job: {currentPlayer.job.name}
                                    <div className="job-description">
                                         {currentPlayer.job.description}
                                    </div>
                                    <div className="job-effects">
                                        <span> Salary: ${currentPlayer.job.effects?.money || 0}</span>
                                    </div>
                                </div>
                            )}
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
};

const styles = `
    .stat-increase {
        animation: glow-green 1s ease-in-out;
    }

    .stat-decrease {
        animation: glow-red 1s ease-in-out;
    }

    @keyframes glow-green {
        0% {
            background-color: rgba(0, 255, 0, 0.2);
        }
        100% {
            background-color: transparent;
        }
    }

    @keyframes glow-red {
        0% {
            background-color: rgba(255, 0, 0, 0.2);
        }
        100% {
            background-color: transparent;
        }
    }

    .top-bar {
        display: flex;
        flex-direction: column; /* Stack items vertically */
        justify-content: center; /* Center items along the main axis (vertical in this case) */
        align-items: center; /* Center items along the cross axis (horizontal in this case) */
        gap: 20px;
        background-color: #222940;
        padding: 20px;
        border-radius: 8px;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
        color: #ffcc00;
    }

    .game-info h2 {
        margin: 0;
        font-size: 1.5em;
        color: #ffcc00;
    }

    .host-indicator {
        font-size: 0.9em;
        color: #ffcc00;
        background-color: rgba(255, 204, 0, 0.2);
        padding: 4px 8px;
        border-radius: 4px;
        margin-top: 5px;
        text-align: center;
    }

    .players-list {
        display: flex;
        flex-wrap: wrap;
        gap: 16px;
        justify-content: center;
    }

    .player-card {
        background-color: #34495e;
        padding: 15px;
        border-radius: 8px;
        transition: transform 0.3s ease, box-shadow 0.3s ease;
        min-width: 250px;
        max-width: 300px;
        text-align: center;
        color: #ecf0f1;
    }

    .player-card:hover {
        transform: translateY(-5px);
        box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
    }

    .player-header {
        margin-bottom: 10px;
        font-weight: bold;
        font-size: 1.2em;
        color: #ffcc00;
    }

    .player-details {
        display: flex;
        flex-direction: column;
        gap: 10px;
    }

    .player-stats-row {
        display: flex;
        justify-content: space-between;
        gap: 8px;
    }

    .player-money,
    .player-hp,
    .player-gender {
        background-color: rgba(0, 0, 0, 0.2);
        padding: 8px 12px;
        border-radius: 4px;
        font-weight: bold;
        color: #ffcc00;
    }

    .player-stats {
        background-color: #2c3e50;
        padding: 10px;
        border-radius: 8px;
        color: #ecf0f1;
    }

    .stats-row {
        display: flex;
        gap: 8px;
        justify-content: space-between;
    }

    .stats-row span {
        flex: 1;
        text-align: center;
        padding: 6px;
        border-radius: 4px;
        background-color: rgba(0, 0, 0, 0.2);
        color: #ffcc00;
    }

    .player-job {
        margin-top: 10px;
        padding: 10px;
        border-radius: 8px;
        background-color: #2c3e50;
        font-weight: bold;
        color: #ecf0f1;
    }

    .job-description {
        font-size: 0.9em;
        font-style: italic;
        color: #bfc9ca;
    }

    .job-effects {
        display: flex;
        flex-wrap: wrap;
        gap: 8px;
        margin-top: 8px;
    }

    .job-effects span {
        flex: 1;
        text-align: center;
        padding: 6px;
        border-radius: 4px;
        background-color: rgba(0, 0, 0, 0.2);
        color: #ffcc00;
    }
`;

const styleSheet = document.createElement("style");
styleSheet.innerText = styles;
document.head.appendChild(styleSheet);

export default React.memo(TopBar);
