import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import socket from '../utils/socket';
import Cookies from 'js-cookie';
import './Lobby.css';

function Lobby() {
    const [players, setPlayers] = useState([]);
    const [isReady, setIsReady] = useState(false);
    const [error, setError] = useState(null);
    const navigate = useNavigate();
    
    const gameCode = sessionStorage.getItem('gameCode');
    const username = sessionStorage.getItem('username');
    const isHost = sessionStorage.getItem('isHost') === 'true';

    useEffect(() => {
        if (!gameCode) {
            navigate('/');
            return;
        }

        const handleHostJoinResponse = (response) => {
            console.log('Host join response:', response);
            if (!response.success) {
                setError(response.error);
                if (response.error === 'Invalid host token') {
                    // Clear invalid host token and redirect
                    Cookies.remove(`hostToken_${gameCode}`);
                    sessionStorage.clear();
                    navigate('/');
                } else if (response.canReconnect) {
                    setError('If you are the host, please try rejoining with the same browser');
                }
            } else {
                if (response.playerToken) {
                    console.log('Setting host token');
                    Cookies.set(`hostToken_${gameCode}`, response.playerToken, {
                        expires: 1,
                        secure: process.env.NODE_ENV === 'production',
                        sameSite: 'strict'
                    });
                }
                if (response.gameState) {
                    sessionStorage.setItem('gameState', JSON.stringify(response.gameState));
                }
                if (response.reconnected) {
                    console.log('Successfully reconnected as host');
                }
            }
        };

        const handlePlayerJoinResponse = (response) => {
            console.log('Player join response:', response);
            if (!response.success) {
                setError(response.error);
                if (response.error === 'Invalid token') {
                    Cookies.remove(`playerToken_${gameCode}`);
                    sessionStorage.clear();
                    navigate('/');
                } else if (response.canReconnect) {
                    setError('If this is your username, please try rejoining with the same browser');
                }
            } else {
                if (response.playerToken) {
                    console.log('Setting player token', response.playerToken);
                    Cookies.set(`playerToken_${gameCode}`, response.playerToken, {
                        expires: 1,
                        secure: process.env.NODE_ENV === 'production',
                        sameSite: 'strict'
                    });
                }
                if (response.players) {
                    setPlayers(response.players);
                }
                if (response.gameState) {
                    sessionStorage.setItem('gameState', JSON.stringify(response.gameState));
                }
                if (response.reconnected) {
                    console.log('Successfully reconnected to game');
                }
            }
        };

        const initializeConnection = () => {
            if (isHost) {
                const hostToken = Cookies.get(`hostToken_${gameCode}`);
                console.log('Initializing host connection with token:', hostToken);
                
                socket.emit('joinHost', {
                    gameCode,
                    playerToken: hostToken
                }, handleHostJoinResponse);
            } else {
                const playerToken = Cookies.get(`playerToken_${gameCode}`);
                console.log('Initializing player connection with token:', playerToken);
                
                socket.emit('joinGame', {
                    gameCode,
                    username,
                    playerToken
                }, handlePlayerJoinResponse);
            }
        };

        // Handle reconnection
        socket.on('connect', () => {
            console.log('Socket connected/reconnected');
            initializeConnection();
        });

        // Initial connection
        if (socket.connected) {
            initializeConnection();
        }

        // Event listeners
        socket.on('playerJoined', ({ players: updatedPlayers }) => {
            console.log('Player joined, updating players list');
            setPlayers(updatedPlayers);
        });

        socket.on('playerReconnected', ({ players: updatedPlayers }) => {
            console.log('Player reconnected, updating players list');
            setPlayers(updatedPlayers);
        });

        socket.on('hostReconnected', ({ gameState }) => {
            console.log('Host reconnected, updating game state');
            if (gameState) {
                sessionStorage.setItem('gameState', JSON.stringify(gameState));
                setPlayers(gameState.players || []);
            }
        });

        socket.on('hostJoined', ({ gameState }) => {
            console.log('New host joined, updating game state');
            if (gameState) {
                sessionStorage.setItem('gameState', JSON.stringify(gameState));
                setPlayers(gameState.players || []);
            }
        });

        socket.on('playerLeft', ({ players: updatedPlayers }) => {
            setPlayers(updatedPlayers);
        });

        socket.on('playersUpdate', ({ players: updatedPlayers }) => {
            setPlayers(updatedPlayers);
        });

        socket.on('hostLeft', () => {
            alert('Host has left the game');
            cleanup();
        });

        socket.on('gameStarted', (response) => {
            console.log('Game started response:', response);
            try {
                if (response && response.gameState) {
                    sessionStorage.setItem('gameState', JSON.stringify(response.gameState));
                }
                navigate('/game');
            } catch (err) {
                console.error('Error handling game start:', err);
                setError('Error starting game');
            }
        });

        socket.on('gameExpired', () => {
            alert('Game has expired');
            cleanup();
        });

        socket.on('error', (error) => {
            console.error('Socket error:', error);
            setError(error.message);
            if (error.type === 'authentication') {
                cleanup();
            }
        });

        return () => {
            socket.off('connect');
            socket.off('playerJoined');
            socket.off('playerReconnected');
            socket.off('hostReconnected');
            socket.off('hostJoined');
            socket.off('playerLeft');
            socket.off('playersUpdate');
            socket.off('hostLeft');
            socket.off('gameStarted');
            socket.off('gameExpired');
            socket.off('error');
        };
    }, [gameCode, username, navigate, isHost]);

    const toggleReady = () => {
        const playerToken = Cookies.get(`playerToken_${gameCode}`);
        if (!playerToken) {
            setError('Authentication required');
            return;
        }

        const newReadyStatus = !isReady;
        setIsReady(newReadyStatus);
        
        socket.emit('playerReady', {
            gameCode,
            username,
            isReady: newReadyStatus,
            playerToken
        }, (response) => {
            if (!response.success) {
                setError(response.error);
                setIsReady(!newReadyStatus);
            }
        });
    };

    const startGame = () => {
        const hostToken = Cookies.get(`hostToken_${gameCode}`);
        if (!isHost || !hostToken) {
            console.log('Not authorized to start game');
            return;
        }

        socket.emit('startGame', {
            gameCode,
            playerToken: hostToken
        });
    };

    const leaveLobby = () => {
        if (isHost) {
            const hostToken = Cookies.get(`hostToken_${gameCode}`);
            if (hostToken) {
                socket.emit('leaveGame', {
                    gameCode,
                    isHost: true,
                    playerToken: hostToken
                });
            }
        } else {
            const playerToken = Cookies.get(`playerToken_${gameCode}`);
            if (playerToken) {
                socket.emit('leaveGame', {
                    gameCode,
                    username,
                    playerToken
                });
            }
        }
        cleanup();
    };

    const cleanup = () => {
        if (isHost) {
            Cookies.remove(`hostToken_${gameCode}`);
        } else {
            Cookies.remove(`playerToken_${gameCode}`);
        }
        sessionStorage.clear();
        navigate('/');
    };

    return (
        <div className="lobby-container">
            <h2>Game Lobby</h2>
            <h3>Game Code: {gameCode}</h3>
            {error && <div className="error-message">{error}</div>}
            
            <div className="players-list">
                <h3>Players:</h3>
                <ul>
                    {players.map((player, index) => (
                        <li key={index}>
                            {player.username} {player.isHost ? '(Host)' : ''} 
                            {player.isReady ? '' : ''}
                        </li>
                    ))}
                </ul>
            </div>

            <div className="lobby-controls">
                {!isHost && (
                    <button onClick={toggleReady}>
                        {isReady ? 'Not Ready' : 'Ready'}
                    </button>
                )}
                
                {isHost && players.length >= 2 && players.every(player => player.isReady || player.isHost) && (
                    <button onClick={startGame}>Start Game</button>
                )}
                
                <button onClick={leaveLobby}>Leave Lobby</button>
            </div>
        </div>
    );
}

export default Lobby;
