import React, { useState } from 'react';

function Choice({ 
    title, 
    description, 
    choices, 
    selectedChoice, 
    onChoiceSelection,
    className = "choice-container" // default class name
}) {
    const [isVisible, setIsVisible] = useState(true);
    const [pincode, setPincode] = useState('');

    const handleChoice = (choice, isPinChoice = false) => {
        if (isPinChoice) {
            if (pincode.length === 3 && /^\d+$/.test(pincode)) {
                setIsVisible(false);
                onChoiceSelection(pincode);
            }
        }
        else {
            setIsVisible(false);
            onChoiceSelection(choice);
        }
    };

    const handlePincodeChange = (e) => {
        const value = e.target.value;
        if (value.length <= 3 && /^\d*$/.test(value)) {
            setPincode(value);
        }
    };

    if (!isVisible) {
        return null;
    }

    return (
        <div className={`${className} ${isVisible ? 'visible' : 'hidden'}`}>
            {title && <h3 className="choice-title">{title}</h3>}
            {description && <p className="choice-description">{description}</p>}
            
            <div className="choice-buttons">
                {choices.map((choice) => (
                    <button 
                        key={choice.id}
                        className={`choice-button ${selectedChoice === choice.id ? 'selected' : ''}`}
                        onClick={() => choice.isPinChoice ? null : handleChoice(choice)}
                    >
                        <div className="choice-content">
                            {choice.imgUrl && (
                                <div className="choice-image">
                                    <img 
                                        src={choice.imgUrl} 
                                        alt={choice.label}
                                        loading="lazy"
                                    />
                                </div>
                            )}
                            <div className="choice-label">
                                {choice.icon && <span className="choice-icon">{choice.icon}</span>}
                                <span>{choice.label}</span>
                            </div>
                            {choice.isPinChoice ? (
                                <div className="pin-input-container">
                                    <input
                                        type="password"
                                        value={pincode}
                                        onChange={handlePincodeChange}
                                        placeholder="Enter 3-digit PIN"
                                        className="pin-input"
                                        maxLength={3}
                                    />
                                    <button
                                        className={`pin-submit ${pincode.length === 3 ? 'active' : ''}`}
                                        onClick={() => handleChoice(null, true)}
                                        disabled={pincode.length !== 3}
                                    >
                                        Set PIN
                                    </button>
                                </div>
                            ) : (
                                choice.description && (
                                    <p className="choice-option-description">{choice.description}</p>
                                )
                            )}
                        </div>
                    </button>
                ))}
            </div>
        </div>
    );
}

// Add this CSS to your styles
const styles = `
    .choice-buttons {
    
    }

    .choice-container {
        opacity: 1;
        transition: opacity 0.3s ease-out;
    }

    .choice-container.hidden {
        opacity: 0;
        pointer-events: none;
    }

    .choice-container.visible {
        opacity: 1;
    }

    .pin-input-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10px;
        margin-top: 10px;
    }

    .pin-input {
        width: 120px;
        height: 40px;
        font-size: 24px;
        text-align: center;
        letter-spacing: 8px;
        border: 2px solid #2ecc71;
        border-radius: 8px;
        background: rgba(46, 204, 113, 0.1);
        color: #2ecc71;
        outline: none;
        transition: all 0.3s ease;
    }

    .pin-input:focus {
        border-color: #27ae60; /* Darker green for focus */
        background: rgba(46, 204, 113, 0.2); /* Slightly darker background */
        box-shadow: 0 0 8px rgba(46, 204, 113, 0.5); /* Subtle glow effect */
        color: #2ecc71;
    }

    .pin-input::placeholder {
        letter-spacing: normal;
        font-size: 14px;
        color: rgba(46, 204, 113, 0.6); /* Muted placeholder color */
    }

    .pin-submit {
        padding: 8px 20px;
        border: 2px solid #2ecc71;
        border-radius: 4px;
        background: transparent;
        color: #2ecc71;
        cursor: pointer;
        transition: all 0.3s ease;
        opacity: 0.5;
    }

    .pin-submit.active {
        opacity: 1;
        background: rgba(46, 204, 113, 0.1);
    }

    .pin-submit.active:hover {
        background: rgba(46, 204, 113, 0.2);
        transform: translateY(-2px);
    }

    .pin-submit:disabled {
        cursor: not-allowed;
        opacity: 0.5;
    }

    .choice-button {
        width: 100%;
        max-width: 300px;
        margin: 10px 0;
    }
`;

// Add styles to document
const styleSheet = document.createElement("style");
styleSheet.innerText = styles;
document.head.appendChild(styleSheet);

export default React.memo(Choice);
