import React, { useState, useEffect, useContext } from 'react';
import socket from '../utils/socket';
import './Map.css';
import { ActionChoicesContext } from '../contexts/ActionChoicesContext';

function Map({ gameState, players, gameCode }) {
    const [currentPlayer, setCurrentPlayer] = useState(null);
    const [diceResults, setDiceResults] = useState(null);
    const [isRolling, setIsRolling] = useState(false);
    const [localPlayers, setLocalPlayers] = useState(players);
    const [currentYear, setCurrentYear] = useState(gameState?.details?.year || 0);
    const { actionChoices, setActionChoices } = useContext(ActionChoicesContext);
    const [boardSquares, setBoardSquares] = useState([]);

    const squareColors = {
        start: '#4CAF50',      // Medium Green (Start)
        upgrade: '#FFC107',    // Bright Gold (Upgrade)
        job: '#2196F3',        // Sky Blue (Job)
        apart: '#BDBDBD',      // Neutral Gray (Apartment default, changes to player's color when bought)
        negative: '#EF5350',   // Vibrant Red (Negative event)
        chance: '#FF9800',     // Bright Orange (Chance)
        ultra_chance: '#FF5722', // Bold Coral (Ultra Chance)
        loss: '#C62828',       // Deep Red (Loss)
        money: '#66BB6A',      // Light Green (Money gain)
        win: '#FFD700',        // Gold (Win)
        minigames: '#00ACC1',  // Fun Teal (Minigames)
    };
    
    const playerColors = [
        '#E53935', // Bright Red
        '#8E24AA', // Deep Purple
        '#1E88E5', // Blue
        '#43A047', // Green
        '#FDD835', // Yellow
        '#FF7043', // Coral Orange
        '#5C6BC0', // Indigo
        '#FFCA28', // Amber
    ];

    useEffect(() => {
        setLocalPlayers(players);
    }, [players]);

    useEffect(() => {
        console.log('Board Squares:', boardSquares);
    }, [boardSquares]);    

    useEffect(() => {
        // Function to fetch boardSquares from game details
        const fetchBoardSquares = () => {
            socket.emit('getBoardSquares', { gameCode }, (response) => {
                if (response.success && Array.isArray(response.boardSquares)) {
                    setBoardSquares(response.boardSquares);
                    console.log('Board squares fetched:', response.boardSquares);
                } else {
                    console.error('Failed to fetch boardSquares:', response.error);
                }
            });
        };
    
        // Listen to board updates from the server
        socket.on('updateBoard', (data) => {
            if (data.gameCode === gameCode && Array.isArray(data.boardSquares)) {
                setBoardSquares(data.boardSquares);
                console.log('Board squares updated:', data.boardSquares);
            } else {
                console.warn('Received invalid or empty boardSquares:', data);
            }
        });
    
        // Fetch initial boardSquares
        fetchBoardSquares();
    
        // Cleanup socket listeners on unmount
        return () => {
            socket.off('updateBoard');
        };
    }, [gameCode]);
    
    useEffect(() => {
        const squareEventHandlers = {
            moneySquare: {
                defaultTitle: "Money Choices",
                defaultDescription: "No valid choices received.",
            },
            upgradeSquare: {
                defaultTitle: "Upgrade Stats",
                defaultDescription: "No valid upgrade choices available.",
            },
            jobSquare: {
                defaultTitle: "Job Selection",
                defaultDescription: "No valid job choices received.",
            },
            chanceSquare: {
                defaultTitle: "Chance Event",
                defaultDescription: "No valid chance events available.",
            },
            lossSquare: {
                defaultTitle: "Loss Event",
                defaultDescription: "No valid loss events available.",
            },
            apartSquare: {
                defaultTitle: "Apartment Square",
                defaultDescription: "No valid choices available for this apartment square.",
            },
            minigamesSquare: {
                defaultTitle: "Minigame Event",
                defaultDescription: "No valid minigame choices available.",
            },
            negativeSquare: {
                defaultTitle: "Negative Event",
                defaultDescription: "No valid negative event choices available.",
            },
            winSquare: {
                defaultTitle: "Win Event",
                defaultDescription: "No valid win events available.",
            },
            ultraChanceSquare: {
                defaultTitle: "Ultra Chance Event",
                defaultDescription: "No valid ultra chance events available.",
            },
            eventChoices: {
                defaultTitle: "Random Event",
                defaultDescription: "Really very random event, out of 10 available.",
            },
        };
    
        const actionChoicesQueue = []; // Queue for action choices
    
        const handleSquareEvent = (eventType, { gameCode, playerId, actionChoices }) => {
            if (socket.id === playerId) {
                console.log(`Received ${eventType} from backend:`, actionChoices);
    
                const { defaultTitle, defaultDescription } = squareEventHandlers[eventType] || {};
                const validatedChoices =
                    actionChoices &&
                    typeof actionChoices.title === "string" &&
                    typeof actionChoices.description === "string" &&
                    Array.isArray(actionChoices.choices) &&
                    actionChoices.choices.length > 0
                        ? actionChoices
                        : {
                              title: defaultTitle || "Unknown Event",
                              description: defaultDescription || "No valid choices available.",
                              choices: [],
                          };
    
                actionChoicesQueue.push(validatedChoices);
                processQueue(); // Attempt to process the queue
            }
        };
    
        const handleVotingThumbEvent = ({ gameCode, playerId, actionChoices }) => {
            console.log("Received voting choices:", { gameCode, playerId, actionChoices });
    
            if (socket.id === playerId) {
                if (
                    actionChoices &&
                    typeof actionChoices.title === "string" &&
                    typeof actionChoices.description === "string" &&
                    Array.isArray(actionChoices.choices) &&
                    actionChoices.choices.length > 0
                ) {
                    setActionChoices(actionChoices);
                } else {
                    console.warn("Invalid voting choices received:", actionChoices);
                    setActionChoices({
                        title: "Voting Choices Error",
                        description: "No valid voting options were provided.",
                        choices: [],
                    });
                }
            } else {
                console.log("Voting prompt ignored for the performer.");
            }
        };
    
        const processQueue = () => {
            if (actionChoicesQueue.length === 0) {
                return;
            }
    
            setActionChoices((currentChoices) => {
                // If current choices are empty, process the next in queue
                if (!currentChoices || currentChoices.choices.length === 0) {
                    const nextChoice = actionChoicesQueue.shift();
                    console.log("Processing next choice from queue:", nextChoice);
                    return nextChoice || { title: "", description: "", choices: [] };
                }
    
                console.log("Current choices still active. Queue will be processed later.");
                return currentChoices;
            });
    
            // Monitor queue and reprocess when choices become empty
            const interval = setInterval(() => {
                if (actionChoicesQueue.length > 0) {
                    setActionChoices((currentChoices) => {
                        if (!currentChoices || currentChoices.choices.length === 0) {
                            const nextChoice = actionChoicesQueue.shift();
                            console.log("Queue reprocessing:", nextChoice);
                            clearInterval(interval); // Stop interval once processed
                            return nextChoice || { title: "", description: "", choices: [] };
                        }
                        return currentChoices;
                    });
                } else {
                    clearInterval(interval);
                }
            }, 100); // Recheck every 100ms
        };
    
        // Register all event listeners
        Object.keys(squareEventHandlers).forEach((eventType) => {
            socket.on(eventType, (data) => handleSquareEvent(eventType, data));
        });
        socket.on("votingThumb", handleVotingThumbEvent);
    
        // Cleanup all listeners
        return () => {
            Object.keys(squareEventHandlers).forEach((eventType) => {
                socket.off(eventType);
            });
            socket.off("votingThumb", handleVotingThumbEvent);
        };
    }, [setActionChoices]);

    useEffect(() => {
        if (!gameCode) return;
    
        // Clean up any existing listeners first
        socket.off('turnUpdate');
        socket.off('diceRolled');
        socket.off('yearUpdate');
    
        // Only emit startTurn on initial mount if needed
        const isFirstTurn = sessionStorage.getItem('gameStarted') !== 'true';
        if (isFirstTurn) {
            socket.emit('startTurn', { gameCode }, (response) => {
                if (response.success) {
                    setCurrentPlayer(response.currentPlayer);
                    setCurrentYear(response.year);
                    sessionStorage.setItem('gameStarted', 'true');
                }
            });
        }
    
        let turnTimer; // Define timer outside event handler
    
        // Event listeners
        socket.on('turnUpdate', ({ currentPlayer, year }) => {
            setCurrentPlayer(currentPlayer);
            setCurrentYear(year);
            setIsRolling(false);
        });
    
        socket.on('yearUpdate', ({ year }) => {
            setCurrentYear(year);
        });
    
        socket.on('diceRolled', ({ playerId, dice1, dice2, newPosition }) => {
            console.log('Dice rolled:', { playerId, dice1, dice2, newPosition });
            setDiceResults({
                playerId,
                dice1,
                dice2,
                total: dice1 + dice2,
                position: newPosition
            });
        
            setIsRolling(false);
        
            if (playerId === socket.id) {
                setTimeout(() => {
                    socket.emit('startTurn', { gameCode });
                }, 1500);
            }
        });
    
        // Cleanup function
        return () => {
            socket.off('turnUpdate');
            socket.off('diceRolled');
            socket.off('yearUpdate');
            if (turnTimer) clearTimeout(turnTimer);
            sessionStorage.removeItem('gameStarted');
        };
    }, [gameCode]);

    const isMyTurn = currentPlayer && currentPlayer.id === socket.id;

    const handleRollDice = () => {
        if (!isMyTurn || isRolling) return;

        setIsRolling(true);
        // Clear any existing action choices before rolling
        setActionChoices(null);
        
        console.log('Rolling dice for player:', socket.id);
        socket.emit('rollDice', { gameCode }, (response) => {
            if (!response.success) {
                console.error('Failed to roll dice:', response.error);
                setIsRolling(false);
            }
        });
    };

    // Rest of your render methods remain the same
    const renderSquare = (index, square) => {
        if (!square || !square.type) {
            console.warn(`Invalid square at index ${index}`, square);
            return <div className="square invalid" key={index}>Invalid Square</div>;
        }
    
        const playersHere = localPlayers.filter(player => player.position === index);
        return (
            <div
                className={`square ${square.type}`}
                key={index}
                style={{ backgroundColor: squareColors[square.type] || '#ccc' }}
            >
                <div className="square-number">{square.number}</div>
                <div className="players-container">
                    {playersHere.map((player, idx) => (
                        <div
                            key={player.id}
                            className={`player ${player.id === currentPlayer?.id ? 'current-player' : ''}`}
                            style={{
                                backgroundColor: playerColors[
                                    localPlayers.findIndex(p => p.id === player.id) % playerColors.length
                                ],
                                transform: `translateX(${idx * 20}%)`,
                            }}
                            title={player.username}
                        />
                    ))}
                </div>
            </div>
        );
    };

    const renderGameInfo = () => (
        <div className="game-info">
            <div className="current-status">
                {gameState.host === socket.id && (
                    <div className="year-display">
                        Year: {currentYear}
                    </div>
                )}
                <div className="turn-info">
                    <span>Current Turn: {currentPlayer?.username || 'Waiting...'}</span>
                </div>
            </div>
            {diceResults && (
                <div className="dice-status">
                    <div className="dice-display">
                        <div className="dice-values">
                            <span> {diceResults.dice1}</span>
                            <span> {diceResults.dice2}</span>
                        </div>
                        <div className="dice-total">
                            Total: {diceResults.total}
                        </div>
                    </div>
                </div>
            )}
        </div>
    );

    const renderControls = () => (
        <div className="controls">
            <div className="turn-status">
                {isMyTurn ? (
                    <span className="your-turn">Your Turn!</span>
                ) : (
                    <span className="waiting">
                        Waiting for {currentPlayer?.username || 'other player'}...
                    </span>
                )}
            </div>
            <button 
                className={`dice-button ${(!isMyTurn || isRolling || actionChoices?.choices?.length > 0) ? 'disabled' : ''}`}
                onClick={handleRollDice}
                disabled={!isMyTurn || isRolling || actionChoices?.choices?.length > 0}
            >
                {isRolling ? 'Rolling...' : 'Roll Dice'}
            </button>
        </div>
    );

    if (!gameState) {
        return <div className="loading">Loading game state...</div>;
    }

    // Player view
    if (gameState.host !== socket.id) {
        return (
            <div className="player-game-view">
                {renderGameInfo()}
                {renderControls()}
            </div>
        );
    }

    // Host view
    return (
        <div className="game-container">
            {renderGameInfo()}
            <div className="board">
                {/* Determine board size */}
                {boardSquares.length <= 24 ? (
                    <>
                        {/* 7x7x7x7 Board Layout */}
    
                        {/* Bottom row */}
                        <div className="row bottom">
                            {boardSquares.slice(0, 7).map((square, index) =>
                                renderSquare(index, square)
                            )}
                        </div>
    
                        {/* Middle rows */}
                        {boardSquares.slice(7, 12).map((square, index) => (
                            <div className="row middle" key={index}>
                                {renderSquare(23 - index, boardSquares[23 - index])}
                                <div className="center-space">
                                    {index === 0 && diceResults && (
                                        <div className="dice-display large">
                                            <div className="dice-values">
                                                <span> {diceResults.dice1}</span>
                                                <span> {diceResults.dice2}</span>
                                            </div>
                                            <div className="dice-total">
                                                Total: {diceResults.total}
                                            </div>
                                        </div>
                                    )}
                                </div>
                                {renderSquare(7 + index, boardSquares[7 + index])}
                            </div>
                        ))}
    
                        {/* Top row */}
                        <div className="row top">
                            {boardSquares.slice(12, 19).map((square, index) => {
                                const reversedIndex = 18 - index;
                                return renderSquare(reversedIndex, boardSquares[reversedIndex]);
                            })}
                        </div>
                    </>
                ) : (
                    <>
                        {/* 10x8x10x8 Board Layout */}
    
                        {/* Bottom row */}
                        <div className="row bottom">
                            {boardSquares.slice(0, 10).map((square, index) =>
                                renderSquare(index, square)
                            )}
                        </div>
    
                        {/* Middle rows */}
                        {boardSquares.slice(10, 18).map((square, index) => (
                            <div className="row middle" key={index}>
                                {renderSquare(35 - index, boardSquares[35 - index])}
                                <div className="large-center-space">
                                    {index === 0 && diceResults && (
                                        <div className="dice-display large">
                                            <div className="dice-values">
                                                <span> {diceResults.dice1}</span>
                                                <span> {diceResults.dice2}</span>
                                            </div>
                                            <div className="dice-total">
                                                Total: {diceResults.total}
                                            </div>
                                        </div>
                                    )}
                                </div>
                                {renderSquare(10 + index, boardSquares[10 + index])}
                            </div>
                        ))}
    
                        {/* Top row */}
                        <div className="row top">
                            {boardSquares.slice(18, 28).map((square, index) => {
                                const reversedIndex = 27 - index;
                                return renderSquare(reversedIndex, boardSquares[reversedIndex]);
                            })}
                        </div>
                    </>
                )}
            </div>
            {renderControls()}
        </div>
    );
}

export default React.memo(Map);