// src/contexts/ActionChoicesContext.js
import React, { createContext, useState } from 'react';

const ActionChoicesContext = createContext();

const ActionChoicesProvider = ({ children }) => {
    const [actionChoices, setActionChoices] = useState({
        title: "Choose your action",
        description: "",
        choices: [],
    });
    const [result, setResult] = useState(null);

    return (
        <ActionChoicesContext.Provider value={{ actionChoices, setActionChoices, result, setResult }}>
            {children}
        </ActionChoicesContext.Provider>
    );
};

export { ActionChoicesContext, ActionChoicesProvider };
