import React, { useState } from 'react';

// Helper function to format effects nicely
const formatEffects = (effects) => {
    return Object.entries(effects).map(([key, value]) => {
        let displayValue;

        // Check if value is an object and format it nicely
        if (typeof value === 'object') {
            // For objects, display each property value (like money, int, str)
            displayValue = Object.entries(value)
                .map(([subKey, subValue]) => `${subKey.charAt(0).toUpperCase() + subKey.slice(1)}: ${subValue}`)
                .join(", ");
        } else {
            displayValue = value;
        }

        return (
            <li key={key} className="event-card-effect-item">
                {`${key.charAt(0).toUpperCase() + key.slice(1)}: ${displayValue}`}
            </li>
        );
    });
};

function EventCard({
    title,
    description,
    choice,
    onChoiceSelection,
    actionChoices,
    className = "event-card-container"
}) {
    const [isVisible, setIsVisible] = useState(true);

    const handleApplyChoice = () => {
        setIsVisible(false);
        onChoiceSelection(choice); // Pass the first choice directly
    };

    if (!isVisible) {
        return null;
    }

    return (
        <div className={`${className} ${isVisible ? 'visible' : 'hidden'}`}>
            {title && <h3 className="event-card-title">{title}</h3>}
            {description && <p className="event-card-description">{description}</p>}

            {actionChoices?.choices?.some((c) => c.minigame) && (
                <div className="minigame-info">
                    <h4>Minigame Rewards</h4>
                    {actionChoices.choices.map((minigame, index) => (
                        minigame.minigame && (
                            <div key={index} className="minigame-details">
                                {/* Display minigame name and description */}
                                <h5>{minigame.label}</h5>
                                <p>{minigame.description}</p>

                                <h6>Win Effects:</h6>
                                <ul>
                                    {formatEffects(minigame.effects.win)}
                                </ul>
                                <h6>Loss Effects:</h6>
                                <ul>
                                    {formatEffects(minigame.effects.loss)}
                                </ul>
                            </div>
                        )
                    ))}
                </div>
            )}

            {choice && (
                <div className="event-card-choice">
                    <button
                        className="event-card-button"
                        onClick={handleApplyChoice}
                    >
                        {choice.icon} {choice.label}
                    </button>
                    {choice.effects && (
                        <div className="event-card-effects">
                            <h4>Effects:</h4>
                            <ul>
                                {formatEffects(choice.effects)}
                            </ul>
                        </div>
                    )}
                </div>
            )}
        </div>
    );
}

// Add this CSS to your styles
const styles = `
    .event-card-container {
        padding: 20px;
        margin: 20px;
        border: 2px solid #3498db;
        border-radius: 10px;
        background: rgba(52, 152, 219, 0.1);
        transition: opacity 0.3s ease-out, transform 0.3s ease;
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
        text-align: center;
    }

    .event-card-container.hidden {
        opacity: 0;
        pointer-events: none;
        transform: scale(0.9);
    }

    .event-card-container.visible {
        opacity: 1;
        transform: scale(1);
    }

    .event-card-title {
        font-size: 24px;
        margin-bottom: 10px;
        color: #ffcc00;
    }

    .event-card-description {
        font-size: 18px;
        margin-bottom: 20px;
    }

    .event-card-button {
        padding: 15px 30px;
        border: none;
        border-radius: 10px;
        background-color: #3498db;
        font-size: 18px;
        cursor: pointer;
        transition: background-color 0.3s ease, transform 0.2s ease;
    }

    .event-card-button:hover {
        background-color: #2980b9;
        transform: scale(1.05);
    }

    .event-card-choice {
        margin-top: 20px;
    }

    .event-card-effects {
        margin-top: 20px;
        font-size: 16px;
    }

    .event-card-effects h4 {
        font-size: 18px;
        margin-bottom: 10px;
    }

    .event-card-effect-item {
        margin-bottom: 5px;
    }

    .minigame-info {
        margin-top: 20px;
        padding: 10px;
        border: 2px dashed #3498db;
        border-radius: 10px;
        background: rgba(52, 152, 219, 0.1);
    }

    .minigame-details {
        margin-bottom: 15px;
    }

    .minigame-details h5 {
        margin-top: 10px;
    }
`;

// Add styles to the document
const styleSheet = document.createElement("style");
styleSheet.innerText = styles;
document.head.appendChild(styleSheet);

export default React.memo(EventCard);
